import { CssAwareElement } from "tds_element";
import { css, html } from "lit";
import { customElement, property } from "lit/decorators.js";
@customElement("tds-contact-card")
export class ContactCard extends CssAwareElement {
  static styles = css`
    :host {
      display: block;
      border-bottom: #f10252 solid 3px;
    }

    .top {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      align-content: space-between;
      align-items: center;
      justify-content: space-between;
      padding-left: 30px;
    }

    .ava {
      width: 30%;
    }

    .ava img {
      width: 100%;
    }

    .names {
      width: 70%;
      padding-left: 20px;
      text-align: left;
    }

    tds-red-bold-text {
      font-weight: 800;
      font-stretch: 100%;
      font-style: normal;
      font-variant-caps: normal;
      font-variant-east-asian: normal;
      font-variant-ligatures: normal;
      font-variant-numeric: normal;
      letter-spacing: normal;
    }

    ul {
      list-style: none;
      padding-left: 30px;
      padding-right: 30px;
      font-size: 14px;
      font-weight: 600;
      text-align: left;
      color: #54595f;
    }

    ul > li {
      margin: 10px 0;
    }

    .email,
    .linkedin,
    .phone {
      padding-left: 28px;
      position: relative;
    }

    .email::before,
    .linkedin::before,
    .phone::before {
      content: "";
      display: inline-block;
      position: absolute;
      left: 0;
      top: 3px;
      width: 14px;
      height: 14px;
      background-repeat: no-repeat;
      background-size: 14px;
    }

    .email::before {
      background-image: url("data:image/svg+xml;base64,");
    }
    .linkedin::before {
      background-image: url("data:image/svg+xml;base64,");
    }
    .phone::before {
      background-image: url("data:image/svg+xml;base64,");
    }

    @media only screen and (max-width: 768px) {
      .top {
        flex-direction: column;
        padding-left: 0;
      }
      .ava {
        width: 160px;
      }
      .names {
        width: 100%;
        padding-left: 0;
      }
      .names {
        text-align: center;
      }
    }
  `;

  @property({ type: String })
  url: string;
  @property({ type: String })
  name: string;
  @property({ type: String })
  title: string;
  @property({ type: String })
  email: string;
  @property({ type: String })
  linkedin: string;
  @property({ type: String })
  phone: string;

  createRenderRoot() {
    /**
     * Render template without shadow DOM. Note that shadow DOM features like
     * encapsulated CSS and slots are unavailable.
     */
    return this;
  }

  render() {
    let formatPhone = this.phone;
    if (formatPhone != "") {
      let p = formatPhone;
      formatPhone = `${p[0]}${p[1]}${p[2]} (0)${p[3]}${p[4]} ${p[5]}${p[6]} ${p[7]}${p[8]} ${p[9]}${p[10]}${p[11]}`;
    }

    return html`
      <div class="top">
        <div class="ava">
          <a href="${this.linkedin}" target="_blank">
            <img src="${this.url}" alt="${this.name}" />
          </a>
        </div>
        <div class="names">
          <a href="${this.linkedin}" target="_blank">
            <tds-red-bold-text>${this.name}</tds-red-bold-text>
          </a>
          <div>
            <tds-lighter-text>${this.title}</tds-lighter-text>
          </div>
        </div>
      </div>
      <ul>
        <li><a href="mailto:${this.email}" class="email">${this.email}</a></li>
        <li>
          <a href="${this.linkedin}" target="_blank" class="linkedin"
            >LinkedIn</a
          >
        </li>
        <li><a href="tel:${this.phone}" class="phone">${formatPhone}</a></li>
      </ul>
    `;
  }
}
