import { CssAwareElement } from "tds_element";
import { css, html } from "lit";
import { customElement, property, query } from "lit/decorators.js";
const mailto = "info@tdscompany.nl";
const subject = encodeURI("TDS_homepage Contact Form Request");

@customElement("tds-contact-from")
export class ContactForm extends CssAwareElement {
  static styles = css`
    * {
      box-sizing: border-box;
    }

    :host {
      display: block;
      text-align: center;
      color: #7a7a7a;
      width: 100%;
      max-width: 400px;
      min-width: 200px;
    }

    *[hidden] {
      display: none;
    }

    label {
      display: block;
      font-weight: 700;
      font-size: 16px;
      line-height: 1.3;
      margin: 10px 0 3px 0;
    }

    .required::after {
      content: "*";
      color: red;
      display: inline;
      margin-left: 5px;
    }

    input,
    textarea {
      display: block;
      font-size: 16px;
      border: 1px solid #ccc;
      padding: 6px 10px;
      width: 100%;
      line-height: 1.3;
    }

    input {
      height: 38px;
    }

    button[type="submit"] {
      margin-top: 20px;
      background: none;
      border: none;
    }

    .success-box {
      margin: 20px 0;
      background: #e0ffc7;
      border: 1px solid #b4d39b;
      color: #333;
      padding: 15px 15px;
    }
  `;

  constructor() {
    super();
    this.success = false;
  }

  createRenderRoot() {
    /**
     * Render template without shadow DOM. Note that shadow DOM features like
     * encapsulated CSS and slots are unavailable.
     */
    return this;
  }

  @property({ type: Boolean })
  success: boolean;

  @query("#form")
  form: HTMLFormElement;

  render() {
    return html`
      <form
        id="form"
        method="post"
        enctype="text/plain"
        action="mailto:${mailto}?subject=${subject}"
        @submit="${this.onformsubmit}"
      >
        <label for="name" class="required">Naam</label>
        <input
          type="text"
          id="name"
          name="name"
          minlength="2"
          maxlength="100"
          required
        />

        <label for="email" class="required">Email</label>
        <input type="email" id="email" name="email" maxlength="100" required />

        <label for="msg" class="required">Bericht</label>
        <textarea
          rows="5"
          id="msg"
          name="msg"
          minlength="10"
          maxlength="1000"
          required
        ></textarea>

        <button type="submit" name="submit">
          <tds-button margin="0 auto">Verzenden</tds-button>
        </button>
        <input type="reset" name="reset" value="Clear Form" hidden />
      </form>

      <div class="success-box" ?hidden="${!this.success}">
        Please follow the instructions in your default email client and send
        auto-generated email. Thanks for contacting us! We will be in touch with
        you shortly.
      </div>
    `;
  }

  resetForm() {
    setTimeout(() => {
      if (typeof this.form.reset === "function") this.form.reset();
      //@ts-ignore
      if (this.form.reset instanceof HTMLInputElement) this.form.reset.click();
    }, 100);
  }

  onformsubmit() {
    this.success = true;
    if (this.isMobileChrome()) {
      this.processMobileClick();
    } else {
      this.resetForm();
    }
  }

  isMobileChrome() {
    let ua = navigator.userAgent;
    let isMobile =
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini|Mobile|mobile|CriOS/i.test(
        ua
      );
    let isChrome = /Chrome/i.test(ua);
    return isMobile && isChrome;
  }

  processMobileClick() {
    //@ts-ignore
    let name = this.form.name ? this.form.name.value : "";
    let email = this.form.email ? this.form.email.value : "";
    let msg = this.form.msg ? this.form.msg.value : "";
    let body = encodeURIComponent(`name: ${name}
email: ${email}
msg: ${msg}`);
    let linkElement = document.createElement("a");
    linkElement.style.visibility = "hidden";
    linkElement.style.position = "absolute";
    linkElement.href = `mailto:${mailto}?subject=${subject}&body=${body}`;
    document.body.appendChild(linkElement);
    linkElement.click();
    linkElement.remove();
    this.resetForm();
  }
}
